import React from 'react'
import Modal from 'react-bootstrap4-modal'
import { compose, renameProp } from 'recompose'

import AppBar from '../Components/AppBar'
import ApplicationList from '../Components/ApplicationList'
import PropertyList from '../Components/PropertyList'

import withResource from '../enhancers/withResource'
import withQueryParser from '../enhancers/withQueryParser'
import withDialog from '../enhancers/withDialog'

const DashboardPage = ({
  applications,
  refetch,

  pending,
  completed,
  setQuery,

  showModal,
  hideModal,
  modal,
}) => (
  <div className="h-100 bg-default">
    <AppBar />
    <div className="row no-gutters" style={{ height: 'calc(100% - 96px)' }}>
      <div className="col-3 h-100">
        <ApplicationList
          applications={applications}
          pending={pending}
          setQuery={setQuery}
          showModal={showModal}
        />
      </div>
      <div className="col-9 h-100">
        <PropertyList
          completed={completed}
          setQuery={setQuery}
          showModal={showModal}
          hideModal={hideModal}
          applicationsRefetch={refetch}
        />
      </div>
    </div>
    <Modal
      onClickBackdrop={hideModal}
      visible={modal.visible}
      children={modal.children ? modal.children({ showModal, hideModal }) : ""}
    />
  </div>
)

export default compose(
  withResource({ resource: 'applications/my' }),
  renameProp('data', 'applications'),
  withQueryParser,
  withDialog,
)(DashboardPage)



