import React from 'react'
import TimeAgo from 'react-timeago'

import statusData from '../helpers/status-data'
import activeClass from '../helpers/active-class'
import applicationClickHelp from '../modals/applicationClickHelp'

const ApplicationItem = ({ showModal }) => ({
  applicantName,
  email,
  status,
  property,
  createdAt,
}, i) => (
  <a
    key={i}
    className="list-group-item list-group-item-action flex-column align-items-start cursor-pointer"
    onClick={() => showModal({ children: applicationClickHelp({ message: statusData[status].message }) })}
  >
    <div className="d-flex w-100 justify-content-between">
      <h6 className="mb-1">{applicantName}</h6>
      <small className="text-secondary"><TimeAgo date={createdAt} /></small>
    </div>
    <small className="d-block mb-1 text-secondary mt-1">{property && property.addressOne}</small>
    <span className={`badge ${statusData[status].className}`}>Status: {statusData[status].text}</span>
  </a>
)

const ApplicationList = ({
  applications,
  showModal,
  hideModal,
  visible,
  pending,
  setQuery,
}) => (
  <div className="list-group h-100">
    <div className="list-group-item bg-secondary mb-1 bb-0">
      <div className="d-flex justify-content-end justify-content-lg-between align-items-center height-35">
        <header className="d-none d-lg-flex text-white font-weight-bold">Applications</header>
        <div>
          <a 
            className={activeClass(pending === 'true', 'mr-3')} 
            onClick={() => setQuery({ pending: true })}
            children="Pending"
          />
          <a
            className={activeClass(pending === 'false')}
            onClick={() => setQuery({ pending: false })}
            children="Completed"
          />
        </div>
      </div>
    </div>
    {applications ? (
      <div className="overflow-y-scroll">
        {
          applications
            .filter((application) => (
              pending === 'true' 
              ? application.status !== 'completed' 
              : application.status === 'completed'
            ))
            .map(ApplicationItem({ showModal }))
        }
      </div>
    ) : (
      <div className="d-flex h-100 p-1 justify-content-center align-items-center text-secondary flex-column">
        <p>There are currently no <br /> <b>applications</b> added right now.</p>
      </div>
    )}
  </div>
)

export default ApplicationList



