import React from 'react'
import { compose, renameProp } from 'recompose'
import TimeAgo from 'react-timeago'

import Form from '../Components/Form'
import addPropertyForm from '../forms/addPropertyForm'
import addPropertyValidator from '../validators/property-validator'
import { addPropertyHandler } from '../handlers/properties'

import withResource from '../enhancers/withResource'
import activeClass from '../helpers/active-class'
import filterFn from '../helpers/filterFn'
import propertyButtonActionData, { propertyButtonActionDataInactive } from '../helpers/property-button-action-data'

let aPForm = ({ fields, refetch }) => ({ showModal, hideModal }) => (
  <div>
    <button
      type="button"
      className="close p-4"
      aria-label="Close"
      onClick={hideModal}
    >
      <span aria-hidden="true">&times;</span>
    </button>
    <Form
      className="p-4"
      validate={addPropertyValidator}
      onSubmit={addPropertyHandler({ hideModal, refetch })}
      fields={fields}
      meta={{ showModal, hideModal }}
    />
  </div>
)

const actionButton = ({ label, ...rest }, i) => (
  <button
    key={i}
    children={label}
    { ...rest }
  />
)

const PropertyItem = ({ showModal, hideModal, applicationsRefetch, refetch }) => ({
  _id,
  addressOne,
  city,
  state,
  zip,
  monthlyAsking,
  createdAt,
  completed,
}, i) => (
<div key={i} className="card">
  <div className="card-body">
    <div className="d-flex justify-content-between">
      <span className="font-weight-bold">{addressOne}</span>
      <small className="text-secondary">
        <TimeAgo date={createdAt} />
      </small>
    </div>
    <span className="d-block text-secondary">{city} {state} {zip}</span>
    <small className="text-secondary">$ {monthlyAsking}</small>
    <hr />
    {completed ? (
      propertyButtonActionDataInactive({ showModal, hideModal, applicationsRefetch, property: _id, refetch }).map(actionButton)
    ) : (
      propertyButtonActionData({ showModal, hideModal, applicationsRefetch, property: _id, refetch }).map(actionButton)
    )}
  </div>
</div>
)

const PropertyList = ({
  properties,
  refetch,
  applicationsRefetch,

  showModal,
  hideModal,
  visible,
  completed,
  setQuery,
}) => (
  <div className="list-group h-100">
    <div className="list-group-item bg-secondary bb-0">
      <div className="d-flex justify-content-end justify-content-lg-between align-items-center height-35">
        <header className="d-none d-lg-flex text-white font-weight-bold">Properties</header>
        <div>
          <a 
            className={activeClass(completed === 'false', 'mr-3')}
            onClick={() => setQuery({ completed: false })}
            children="Active"
          />
          <a
            className={activeClass(completed === 'true', 'mr-3')}
            onClick={() => setQuery({ completed: true })}
            children="Inactive"
          />
          <button
            type="button"
            className="btn btn-sm btn-dark"
            children="+ Add Property"
            onClick={() => showModal({ children: aPForm({ fields: addPropertyForm, refetch }) })}
          />
        </div>
      </div>
    </div>

    {properties ? (
      <div
        className="card-columns p-3"
        children={(properties.filter(filterFn({
            completed: completed === 'true' ? true : false
          })).map(PropertyItem({ showModal, hideModal, applicationsRefetch, refetch }))
        )}
      />
    ) : (
      <div className="d-flex h-100 p-1 justify-content-center align-items-center text-secondary flex-column">
        <p>There are currently no <br /> <b>properties</b> added right now.</p>
      </div>
    )}
  </div>
)


export default compose(
  withResource({ resource: 'properties' }),
  renameProp('data', 'properties')
)(PropertyList)



