import React, { Component } from 'react';
import AppBar from './AppBar.js';
import Footer from './Footer.js';

export default class Login extends Component {
  constructor(props){
    super(props);
    this.state={
      loggedIn: false,
      username: '',
      password: '',
      userInfo: null,
      test: 'Login',
      attempted: null
    }
  }



  userLogin() {
    console.log('#######################', this.state)
    var config = {
    	email: this.state.username,
      password: this.state.password
    }
    fetch('https://polar-caverns-23447.herokuapp.com/login', {
      method: 'POST',
      headers: {
        'content-type': 'application/json'
      },
      body: JSON.stringify(config)
    })
    .then((res)=>{
      return res.json();
    })
    .then((res)=>{
      if (res.errors) {
        this.setState({attempted: true});
      } else {
        localStorage.setItem('userName', res.email);
        localStorage.setItem('auth', JSON.stringify(res));
        window.location = '/dashboard';
      }
      console.log('res inside of login is: ', res);
      if (res.email === this.state.username) {
        console.log('dash')
      }
      console.log('successfully logged in ' + localStorage.getItem('userName'))
    })
  }

  render(){
    return (
      <div className="bg-default">
        <AppBar />
          <h1 style={{fontWeight: 'bold', textAlign: 'center', width: '40%', marginRight: '30%', marginLeft: '30%', marginTop: 30}}>About Us</h1>
          <h3 style={{textAlign: 'center', width: '40%', marginRight: '30%', marginLeft: '30%', marginBottom: 45}}>--- Our Story ---</h3>

            <div style={{marginBottom: 50, backgroundColor: '#f8f8f8', display: 'flex', width: '50%', marginLeft: '25%', marginRight: '25%', textAlign: 'center'}}>
              <p>
                My name is Tristyn Leos. After working as a Texas Realtor for 3 years, I founded Apply Now Leasing in 2016. I realized that leases could sometimes
                take MORE work than a 1-4 Family transaction. With the money orders and pay stubs you have to collect. Especially in a big city like Austin, there was a
                lot of running around to do! So I created a way to simplify the leasing process. And I believe that Apply Now Leasing is the easiest, simplest way to run background checks on lease applicants.
                Apply Now Leasing was created by Realtors, for Realtors. And we strive
                to make the laesing process as simple as possible. We are always looking for feedback from our community Realtors!
              </p>
            </div>
          <Footer />
      </div>
    )
  }
}



